.mv {
  opacity: 0;
  animation: fadeIn 3s ease forwards;
  position: relative;
  height: 100vh;
  height: 100svh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: "image" "title";
  grid-template-rows: min-content 1fr;
}
@media screen and (min-width: 768px) {
  .mv {
    align-items: center;
    grid-template-columns: 50% 50%;
    grid-template-areas: "title image";
    grid-template-rows: auto;
  }
}
@media (prefers-reduced-motion: reduce) {
  .mv {
    opacity: 1;
    animation: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
body.is-loaded .mv {
  opacity: 1;
  animation: none;
}

.mv::after {
  content: "";
  position: absolute;
  top: calc(100vh - 6.25rem);
  left: 0;
  width: 50%;
  aspect-ratio: 360/280;
  background: url(../images/common/shape_1.png) center center/contain no-repeat;
}
@media screen and (min-width: 768px) {
  .mv::after {
    top: calc(100vh - 13.75rem);
    width: 30%;
  }
}

.mv__logo {
  position: absolute;
  top: 1.25rem;
  left: 0.9375rem;
  max-width: 6.75rem;
  width: 100%;
  z-index: 100;
}
@media screen and (min-width: 768px) {
  .mv__logo {
    top: 3.75rem;
    left: 2.5rem;
    max-width: 8.75rem;
  }
}

.mv__image {
  position: relative;
  align-self: start;
  justify-self: right;
  width: clamp(18.75rem, 9.947rem + 37.559vi, 43.75rem);
  aspect-ratio: 701/740;
}

.mv__image img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 2;
}

.mv__inner {
  display: grid;
  place-content: center;
  height: 100%;
}
@media screen and (min-width: 768px) {
  .mv__inner {
    text-align: center;
  }
}

.mv__title {
  position: relative;
  display: grid;
  font-size: clamp(2rem, 1.648rem + 1.502vi, 3rem);
  font-family: "Zen Maru Gothic", sans-serif;
  font-weight: 400;
  line-height: 2.5;
  transform: translateY(-20%);
}
@media screen and (min-width: 768px) {
  .mv__title {
    line-height: 3;
    transform: none;
  }
}

.mv__title span {
  display: inline-block;
}

.mv__title span:last-child {
  position: relative;
  text-indent: clamp(4.375rem, 3.055rem + 5.634vi, 8.125rem);
}

.mv__title::before,
.mv__title::after,
