@use "global" as *;

.lesson-card {
  position: relative;
  background: $white;
  height: 100%;
  display: flex;
  flex-direction: column;
  border-radius: 2rem;
  box-shadow: 0.16em 0.16em 0.8em rgba(90, 62, 43, 0.25);
  transition: transform .3s cubic-bezier(.25,.46,.45,.94);
}

.lesson-card__badge{
  position: absolute;
  top: rem(-30);
  left: rem(-10);
  background: linear-gradient(to top, rgba(255,255,255,0.15), transparent),$ac-color;
  width: rem(100);
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  @include mq("md"){
    left: rem(-20);
  }
}

.lesson-card__month{
  font-size: rem(20);
  color: $white;
}

.lesson-card__image {
  @include mq("md"){
    max-width: rem(306);
  }
}

.lesson-card__image img {
  width: 100%;
  height: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 2rem 2rem 0 0;
  transition: opacity .3s ease;
}

.lesson-card__body {
  padding: rem(16);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  @include mq(md) {
    padding: rem(24) rem(20) rem(30);
  }
}

.lesson-card__category {
  display: inline-block;
  align-self: flex-start;
  padding: rem(8) rem(14);
  font-size: rem(12);
  line-height: 1;
  border: 0.08em solid;
  border-radius: 2em;
  @include mq(md) {
    padding: rem(8) rem(16);
  }
}

.lesson-card__title {
  margin-top: rem(8);
  font-size: rem(20);
  font-weight: $regular;
}

.lesson-card__schedule {
  margin-top: rem(8);
}

.lesson-card--sidebar .lesson-card__badge{
  left: auto;
  right: rem(-10);
  @include mq("md"){
    right: rem(-20);
  }
}

.lesson-card--sidebar .lesson-card__title{
  padding-bottom: 0;
  border-bottom: none;
}

.lesson-card:hover{
  @include mq("md"){
    transform: translateY(rem(-4));
  }
}

.lesson-card:hover img {
  @include mq("md"){
    opacity: 0.8;
  }
}