@use "global" as *;

.archive-list {
  font-size: rem(20);
}

.archive-list__year-button {
  position: relative;
  padding: rem(6) rem(16);
  width: 100%;
  text-align: left;
  color: $font-color;
  background-image: url('../images/common/line-thin_360.png');
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 100% 0.1em;
}

.archive-list__year-button::after {
  content: '';
  position: absolute;
  top: 50%;
  right: rem(16);
  width: rem(10);
  height: rem(10);
  border-right: 0.08em solid;
  border-bottom: 0.08em solid;
  transform: translateY(-50%) rotate(45deg); 
  transition: transform .3s ease;
}

.archive-list__months{
  overflow: hidden;
  height: 0;
  opacity: 0;
  transition: height .6s ease, opacity .4s ease .2s;
}

.archive-list__month{
  padding: rem(6) rem(32);
}

.archive-list__year.is-open .archive-list__year-button::after {
  transform: translateY(-50%) rotate(-135deg);
}

.archive-list__year-button:hover,
.archive-list__month:hover{
  @include mq("md"){
    opacity: 0.7;
  }
}

.archive-list__year-button:focus-visible,
.archive-list__month:focus-visible{
  outline: 3px solid #1a73e8;
  outline-offset: 4px;
  border-radius: 6px;
}